.login-form{
	margin:auto;
	margin-top:300px;
	border:1px solid #000;
	width:300px;
	padding:20px;
	background-color: #fff
}
body{
	background:#bbb;
	margin:0;
	padding:0;
}
.left-menu{
	box-sizing: border-box;
	width:200px;
	border:1px solid #000;
	background-color:#fff;
	position:absolute;
	margin-top:105px;
	min-height: calc(100% - 124px);
}
.left-menu ul{
	padding-left:0px;
	margin:0;
	list-style-type: none;
}
.left-menu ul li{
	border-top:1px solid #000;
	border-bottom:1px solid #000;
	padding:4px;
}
.header{
	box-sizing: border-box;
	width:100%;
	height:100px;
	border:1px solid #000;
	background-color:#fff;
	position:absolute;
	padding:10px;
}
.header .logout-button{
	float:right;

}
.main-window{
	box-sizing: border-box;
	margin-top:105px;
	margin-left:204px;
	border:1px solid #000;
	background:#fff;
	width:calc(100% - 204px);
	min-height: calc(100% - 124px);
	position: absolute;
	padding:20px;
}

table.user-list{
	border:1px solid;
	border-collapse: collapse;
	width:100%;
}
table.user-list th{
	background:#acacac;
}
table.user-list td, table.user-list th{
	border:1px solid #000;
	padding:5px;
}
